import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

import TextField from 'muse-ui/textField';
import checkbox from 'muse-ui/checkbox';

import { ISubmitImpression } from '~/entity/friendImpressions';
import { IAddFriendImpParams } from '~/entity/route_params/index';
import { ICommentViewParams } from '~/entity/route_params';
import { ToastMsg } from '~/entity/toast';
import Api from '~/api';

interface IAddFriendImp {
    submitImpression: ISubmitImpression;
    tags: {
        text: string;
        selected: boolean;
    }[];
    inputTags: string;
    selectCount: number;
    sex: number;
    isableCommit: boolean;
    isableAddTags: boolean;
    privacyCheck: boolean;
}
// constructor(data: ISubmitImpression) {
//     for (const key in data) {
//         this.submitImpression[key] = data[key];
//     }
// }
@Component({
    components: {
        'mu-text-field': TextField,
        'mu-checkbox': checkbox
    }
})
export default class AddFriendImpression extends Vue {
    evaluationData: IAddFriendImp = {
        submitImpression: new ISubmitImpression(),
        tags: [],
        inputTags: null,
        selectCount: 0,
        sex: null,
        isableCommit: true,
        isableAddTags: true,
        privacyCheck: false
    };    
    maxTagLength = 6;
    paramsAddFriendImp: IAddFriendImpParams = null;
    inputTagsErrorText: string = '';
    contentInputErrorText: string = '';

    @Action('showToast')
    showToast;
    @Action('showLoading')
    showLoading;
    @Action('hideLoading')
    hideLoading;

    get isableAddTags() {
        if (this.evaluationData.inputTags && (this.evaluationData.inputTags.length > 0 && this.evaluationData.inputTags.length < this.maxTagLength + 1)) {
            return true;
        } else {
            return false;
        }
    }

    activated() {
        this.paramsAddFriendImp = this['$route'].params;
        this.initData();
        this.evaluationData.submitImpression.uid = this.paramsAddFriendImp.id;   
        this.evaluationData.privacyCheck = this.paramsAddFriendImp.action === 'fromFriendList'; 
        this.getEvaluationTags();
    }

    initData() {
        this.evaluationData = {
            submitImpression: new ISubmitImpression(),
            tags: [],
            inputTags: null,
            selectCount: 0,
            sex: null,
            isableCommit: true,
            isableAddTags: true,
            privacyCheck: false
        };
        this.maxTagLength = 6;
        this.inputTagsErrorText = '';
        this.contentInputErrorText = '';
    }
    
    // 获取系统备选标签
    async getEvaluationTags() {
        this.showLoading();
        try {
            const tagsRes = await Api.getImpressionTagsBySex(this.paramsAddFriendImp.sex);
            this.hideLoading();
            const tagArray = tagsRes.body.obj.characterTagss;
            tagArray.forEach(element => {
                this.evaluationData.tags.push({
                    text: element,
                    selected: false
                });
            });
        } catch (e) {
            this.hideLoading();
            this.showToast(new ToastMsg(e.message));
        }
    }

    // 更改所选标签状态
    toggleTag(tag) {
        if (tag.selected) {
            this.evaluationData.selectCount -= 1;
            console.info(this.evaluationData.selectCount);
            tag.selected = !tag.selected;
        } else {
            if (this.evaluationData.selectCount >= 6) {
                this.showToast(new ToastMsg('印象标签最多只能选6个'));
            } else {
                this.evaluationData.selectCount += 1;
                console.info(this.evaluationData.selectCount);
                tag.selected = !tag.selected;
            }
        }
    }
    // 添加自定义标签
    addEvaluationTag() {
        // const tagList = this.evaluationData.tags; // {text, likes, selected}
        if (this.evaluationData.inputTags && this.evaluationData.inputTags !== '') {
            if (this.evaluationData.selectCount >= 6) {
                this.showToast(new ToastMsg('印象标签最多只能选6个'));
            } else {
                // 检查输入标签与现有标签是否一致
                let length;
                let i;
                for (i = 0, length = this.evaluationData.tags.length; i < length; i++) {
                    if (this.evaluationData.inputTags === this.evaluationData.tags[i].text) {
                        this.evaluationData.tags[i].selected = true;
                        break;
                    }
                }
                if (i === length) {
                    this.evaluationData.tags.unshift({ text: this.evaluationData.inputTags, selected: true });
                }
                this.evaluationData.selectCount += 1;
                console.info(this.evaluationData.selectCount);
                this.evaluationData.inputTags = null; // 清空输入框内容
            }
        }
    }

    // 字数超出提示
    handleInputTagsOverflow(isOverflow) {
        // this.inputTagsErrorText = isOverflow ? '最多输入6个字' : '';
        this.evaluationData.isableAddTags = !isOverflow;
    }
    handleContentOverflow(isOverflow) {
        this.contentInputErrorText = isOverflow ? '超过字数限制啦！' : '';
        this.evaluationData.isableCommit = !isOverflow;
    }

    confirmBack() {             
        const params: ICommentViewParams = {
            id: this.paramsAddFriendImp.id,
            action: this.paramsAddFriendImp.action
        };
        this['$router'].replace({
            name: 'commentView',
            params
        });
    }
    cancelEvalation() {  
        history.back();
        // if (this.paramsAddFriendImp.action === 'fromFriendList') {
        //     const params: ICommentViewParams = {
        //         id: this.paramsAddFriendImp.id,
        //         action: this.paramsAddFriendImp.action
        //     };
        //     this['$router'].replace({
        //         name: 'commentView',
        //         params
        //     });
        // } else {
            // const params: ICommentViewParams = {
            //     id: this.paramsAddFriendImp.id,
            //     action: this.paramsAddFriendImp.action
            // };
            // this['$router'].replace({
            //     name: 'commentView',
            //     params
            // });
        // }        
    }

    async confirmEvalation() {
        if ((this.evaluationData.submitImpression.content === null) || (this.evaluationData.submitImpression.content === '')) {
            this.showToast(new ToastMsg('请输入一句话评论'));
        } else {
            let tag = null;
            let tagIndex = 0;
            for (let i = 0; i < this.evaluationData.tags.length; i++) {
                tag = this.evaluationData.tags[i];
                if (tag.selected) {
                    this.evaluationData.submitImpression['tag' + tagIndex] = tag.text;
                    tagIndex++;
                }
            }
            if (tagIndex < 6) {
                for (let i = tagIndex; i < 6; i++) {
                    this.evaluationData.submitImpression['tag' + tagIndex] = null;
                    tagIndex++;
                }
            }
            this.evaluationData.privacyCheck ? this.evaluationData.submitImpression.privacy = 3 : null; // 若为私密留言，则需要传权限类型为3
            console.info(this.evaluationData.submitImpression);
            this.showLoading();
            try {
                const commintRes = await Api.commitNewSentences(this.evaluationData.submitImpression);                
                this.hideLoading();
                this.evaluationData.tags = [];
                this.evaluationData.inputTags = '';
                this.evaluationData.submitImpression.content = null;
                this.confirmBack();
            } catch (e) {
                this.hideLoading();
                this.showToast(new ToastMsg(e.message));
            }
        }
    }
}



